import owClendar from './ow-calendar.vue';

const owClendarWrap = {
  install(Vue, options) {
    Vue.component(owClendar.name, owClendar) // vuePayKeyboard.name name //  this.$xxx   // Vue.prototype.$xxx //  this.$xxx  // $  } } export default paykeyboard // ..
  }
}

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(owClendarWrap);
}
export default owClendarWrap;


